<template>
  <div class="signature-creator">
    <form class="form" @submit.prevent="copy">

      <div class="form-group">
        <label for="name">Name</label>
        <input @focus="reset" name="name" type="text" v-model="name">
      </div>
      <div class="form-group">
        <label for="title">Title</label>
        <input @focus="reset" name="title" type="text" v-model="title">
      </div>
      <div class="form-group">
        <label for="ext">Phone Ext</label>
        <input @focus="reset" name="ext" type="number" v-model="ext">
      </div>
      <div class="form-group">
        <label for="email">Email</label>
        <input @focus="reset" name="email" type="email" v-model="email">
      </div>
      <div class="form-group">
        <label for="twitter">Twitter</label>
        <input @focus="reset" name="twitter" type="text" v-model="twitter">
      </div>
      <div class="form-group">
        <label for="linkedin">Linkedin</label>
        <input @focus="reset" name="linkedin" type="text" v-model="linkedin">
      </div>
      <div class="form-group">
        <label for="github">Github</label>
        <input @focus="reset" name="github" type="text" v-model="github">
      </div>
      <div class="form-group">
        <button type="submit" @click="copy">Copy Now</button> <span class="success" v-if="successful">Success!</span> <span class="error" v-if="errorMsg">{{ errorMsg }}</span>
      </div>
    </form>
    <div class="signature-wrapper" :contenteditable="selecting" @click="noClick">
      <br align="center" class="float-center"><br align="center" class="float-center"><br align="center" class="float-center"><table class="row signature-page" style="Margin:0;border-collapse:collapse;border-spacing:0;float:none;margin:0;max-width:500px;padding:0;position:relative;text-align:center;vertical-align:top;width:500px"><tbody><tr style="padding:0;text-align:left;vertical-align:top"><th class="small-4 large-4 columns first" valign="middle" style="Margin:0 auto;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:400;line-height:1.3;margin:0 auto;padding:0;padding-bottom:16px;padding-left:0;padding-right:16px;text-align:left;width:177px"><table style="border-collapse:collapse;border-spacing:0;padding:0;text-align:left;vertical-align:top;width:100%"><tr style="padding:0;text-align:left;vertical-align:top"><th style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left"><a target="_blank" rel="noopener" href="https://www.classyllama.com/" style="Margin:0;background:#fff;color:#777;display:block;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/CL_Logo_Square_-_Light1.67x.png" alt="Classy Llama" width="120" height="120" align="right" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:block;max-width:100%;outline:0;text-decoration:none;width:auto"></a></th></tr></table></th><th class="small-8 large-8 columns last" style="Margin:0 auto;background:#fff;border-left:1px solid #d8d8d8;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:400;line-height:1.3;margin:0 auto;padding:0;padding-bottom:8px;padding-left:16px;padding-right:16px;padding-top:8px;text-align:left;width:370.67px"><table style="border-collapse:collapse;border-spacing:0;padding:0;text-align:left;vertical-align:top;width:100%"><tr style="padding:0;text-align:left;vertical-align:top"><th style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left"><p class="lead" style="Margin:0;Margin-bottom:10px;background:#fff;color:#333;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:21px;font-weight:800;line-height:24px;margin:0;margin-bottom:0;padding:0;text-align:left">{{ name }}</p><p style="Margin:0;Margin-bottom:10px;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:15px;font-weight:400;line-height:24px;margin:0;margin-bottom:0;padding:0;text-align:left">{{ title }}, Classy Llama</p><p class="small" style="Margin:0;Margin-bottom:10px;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left">&nbsp;</p><p v-if="ext" class="small" style="Margin:0;Margin-bottom:10px;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" href="tel:14178668887" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/phone2x.png" alt="" width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto">&nbsp;(417) 866-8887 </a>ext {{ ext }}</p><p v-if="email" class="small" style="Margin:0;Margin-bottom:10px;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="mailto" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/mail2x.png" alt="" width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto">&nbsp;{{ email }}</a></p><p v-if="linkedin" class="small" style="Margin:0;Margin-bottom:10px;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="linkedinHref" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/linkedin2x.png" alt="" width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto">&nbsp;{{ linkedin }}</a></p><p v-if="twitter" class="small" style="Margin:0;Margin-bottom:10px;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="twitterHref" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/twitter2x.png" alt="" width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto">&nbsp;{{ twitter }}</a></p><p v-if="github" class="small" style="Margin:0;Margin-bottom:10px;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="githubHref" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/github2x.png" alt="" width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto">&nbsp;{{ github }}</a></p></th></tr></table></th></tr></tbody></table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'SignatureCreator',
    data() {
      return {
        name: 'Your Name',
        title: 'Your Title',
        ext: '',
        email: 'firstname.lastname@classyllama.com',
        twitter: '',
        linkedin: '',
        github: '',
        selecting: false,
        successful: null,
        errorMsg: null
      }
    },
    computed: {
      mailto() {
        return `mailto:${this.email}`;
      },
      twitterHref() {
        return `https://twitter.com/${this.twitter}`;
      },
      linkedinHref() {
        return `https://www.linkedin.com/in/${this.linkedin}`;
      },
      githubHref() {
        return `https://github.com/${this.github}`;
      }
    },
    methods: {
      copy() {
        this.selecting = true;
        var el = document.querySelector('.signature-wrapper')
        var range = document.createRange();
        range.selectNodeContents(el);
        var sel = window.getSelection();
        sel.removeAllRanges();
        sel.addRange(range);

        try {
          this.successful = document.execCommand('copy');
          this.errorMsg = null;
        } catch (err) {
          this.successful = false;
          this.errorMsg = err;
        }
        sel.removeAllRanges();
        this.selecting = false;
      },
      reset() {
        this.successful = null;
        this.errorMsg = null;
      },
      noClick() {
        this.errorMsg = '← Just click the COPY NOW button.'
      }
    }
  }
</script>

<style scoped>
  .form {
    font-size: 16px;
    line-height: 24px;
  }
  .form-group {
    margin-bottom: 16px;
  }
  input {
    font-size: 16px;
    height: 16px;
    padding: 7px 7px;
    border: 1px solid #ddd;
    border-radius: 2px;
  }
  label {
    display: inline-block;
    width: 150px;
    margin-right: 5px;
    color: #777777;
    font-weight: 600;
    padding: 4px 0;
    text-align: right;
  }
  button {
    border: 0;
    background: #52D4DB;
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    border-radius: 5px;
    padding: 0 16px;
    margin-left: 155px;
    margin-right: 16px;
  }
  button:hover,
  button:focus {
    cursor: pointer;
    background: #74E6EC;
    outline: none;
  }
  .signature-wrapper {
    padding: 15px;
    background: #fff;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  .success {
    color: #01D296;
  }
  .error {
    color: #E23B2C;
  }
</style>


